nav {
  background: #9E9CED;
  padding: 10px;
  border: 5px solid  #392242;
}

nav ul {
  list-style: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 20px;
}

nav ul li a  {
  color: #fff;
  text-decoration: none;
  font-weight: 700;
  font-size: 24px;
}
a:hover {
  color: #557ECB;
  transition: 0.3s ease;
}
.wholeToggle {
  color: #fff;
  text-decoration: none;
  font-weight: 700;
  font-size: 24px;

}
.tlToggle {
  color: #fff;
  text-decoration: none;
  font-weight: 700;
  font-size: 20px;
  background-color: transparent;
  border: none;
  cursor: url('pointercursor.png'), auto;
}
.tlToggle:hover {
  color: #557ECB;
  transition: 0.3s ease;
}


@media (max-width: 768px) {
  nav ul {
    flex-direction: column;
    align-items: center; 
    gap: 5px;
  }

  nav ul li {
    margin: 10px 3px;
  }

  nav ul li a {
    font-size: 10px;
  }
}

@media (max-width: 480px) {
  nav {
    padding: 5px;
    border-width: 3px;
    gap: 2px;
  }

  nav ul li a {
    font-size: 13px;
  }
  .wholeToggle, .tlToggle {
    font-size: 13px;
  }
  .tlToggle {
    margin-left: -3px;
  }
}

@media (max-width: 360px) {
  nav {
    padding: 5px;
    border-width: 3px;
    gap: 2px;
  }

  nav ul li a {
    font-size: 11px;
  }
  .tlToggle {
    margin-left: -3px;
  }
}