@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200..1000;1,200..1000&display=swap');
* {
  font-family: 'Mulish', sans-serif;
  scrollbar-width: thin;
  scrollbar-color: #FFA0DD #392242 ;
}
a {
  cursor: url('pointercursor.png'), auto;
}
a:hover {
  cursor: url('pointercursor.png'), auto;
}
body {
  background-image:  linear-gradient(#ffffff 1px, transparent 1px), linear-gradient(to right, #ffffff 1px, #FFB8DA 1px);
  background-size: 50px 50px;
  cursor: url('maincursor.png'), auto;
}


.circles{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}

.circles li{
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  /*background: rgba(255, 255, 255, 0.8);*/
  animation: animate 25s linear infinite;
  bottom: -150px;
  box-shadow: 1px 3px 3px rgba(39, 20, 0, 0.5);
  
}

.circles li:nth-child(1){
  left: 25%;
  width: 10px;
  height: 10px;
  animation-delay: 0s;
  background: #ffce63;
  border: 1px solid rgb(255, 255, 255);
}


.circles li:nth-child(2){
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
  background: #FFA0DD;
  border: 1px solid rgb(255, 255, 255);
}

.circles li:nth-child(3){
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
  background: #C1A5ED;
  border: 1px solid rgb(255, 255, 255);
}

.circles li:nth-child(4){
  left: 40%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
  animation-duration: 18s;
  background: #ffce63;
  border: 1px solid rgb(255, 255, 255);
}

.circles li:nth-child(5){
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
  background: #FFA0DD;
  border: 1px solid rgb(255, 255, 255);
}

.circles li:nth-child(6){
  left: 75%;
  width: 10px;
  height: 10px;
  animation-delay: 3s;
  background: #C1A5ED;
  border: 1px solid rgb(255, 255, 255);
}

.circles li:nth-child(7){
  left: 35%;
  width: 30px;
  height: 30px;
  animation-delay: 7s;
  background: #FFA0DD;
  border: 1px solid rgb(255, 255, 255);
}

.circles li:nth-child(8){
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
  background: #C1A5ED;
  border: 1px solid rgb(255, 255, 255);
}

.circles li:nth-child(9){
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
  background: #FFA0DD;
  border: 1px solid rgb(255, 255, 255);
}

.circles li:nth-child(10){
  left: 85%;
  width: 30px;
  height: 30px;
  animation-delay: 0s;
  animation-duration: 11s;
  background: #ffce63;
  border: 1px solid rgb(255, 255, 255);
}



@keyframes animate {

  0%{
      transform: translateY(0) rotate(0deg);
      opacity: 1;
      border-radius: 100%;
  }

  100%{
      transform: translateY(-1000px) rotate(720deg);
      opacity: 1;
      border-radius: 100%;
  }

}